body {
    font-family: 'Arial', sans-serif;
    background-color: #f5f5f5;
    text-align: center;
  }
  
  .container {
    max-width: 400px;
    margin: 20px auto;
    background: white;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }
  
  h2 {
    color: #333;
  }
  
  input {
    width: 80%;
    padding: 8px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  button {
    padding: 8px 12px;
    border: none;
    cursor: pointer;
    margin: 5px;
    border-radius: 5px;
  }
  
  .add-btn {
    background-color: #28a745;
    color: white;
  }
  
  .delete-btn {
    background-color: #dc3545;
    color: white;
  }
  
  .card {
    color: brown;
    display: flex;
    justify-content: space-between;
    background: antiquewhite;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
  }
  